module.exports = [{"attributes":{"name":"3ree-dashboard","title":"3REE Dashboard","tags":["Fullstack","FrontEnd","BackEnd","React","Redux","Express","Node.js","RethinkDB","Bootstrap","Socket.io","Webpack","C3.js"],"links":{"github":"https://github.com/horizon0708/rethink-dashboard-back","link":"https://rethink-dashboard.herokuapp.com/"}},"body":"## About\nThis is a 3REE(RethinkDB, React, Redux, Express) stack application that tracks and visualises changes in databases. Using RethinkDB's unique changefeed feature, I built a secondary database for logging that logged numbers of users after every changes in the primary user DB.\n\nFront-end components are built with React-Redux, and are updated as soon as there is a change in the DB. The updates are pushed with socket.io and stored in a Redux store. C3.js is used to visualise data.\n\n## Goals\n- Learn real-time data-visualisation using C3.js, socket.io and RethinkDB.\n- Learn more about back-end development and deployment.\n- Familiarise myself more with Redux. \n\n## Post Mortem\n- I realised the importance of having a good development environment. Tooling was time-consuming and hard, and I have appreciated how amazing create-react-app was. I would like to be able to set up an automated back-end development environment.\n- Deploying can be a nightmare. 'Works on my machine' problem is very real. I still have one amazingly weird bug (ask me for more details!)\n- I've also appreciated how good ESLint is at keeping my code clean.\n\n## Future goals\n- Learn more about D3.js and websockets. While C3.js and Socket.io are amazing abstractions, I feel like I would be able to do much more if I understood web-sockets and d3.js.\n- Learn more about Docker. I would like future deployments to be less painful.\n\n\n\n\n\n","frontmatter":"name: 3ree-dashboard\ntitle: 3REE Dashboard\ntags: \n- Fullstack\n- FrontEnd\n- BackEnd\n- React\n- Redux\n- Express\n- Node.js\n- RethinkDB\n- Bootstrap\n- Socket.io\n- Webpack\n- C3.js\nlinks:\n    github: https://github.com/horizon0708/rethink-dashboard-back \n    link: https://rethink-dashboard.herokuapp.com/"},{"attributes":{"name":"markdown-viewer","title":"Markdown Viewer","tags":["FrontEnd","React","Codepen"],"links":{"link":"https://codepen.io/horizon0708/pen/dRmZbR"}},"body":"## About\nA project for FreeCodeCamp Front-end certification, this app formats markdown text into html real-time, using Marked and React.\n\n## Goals\n- Learn how to create a react app with controlled input. \n","frontmatter":"name: markdown-viewer\ntitle: Markdown Viewer\ntags: \n- FrontEnd\n- React\n- Codepen\nlinks:\n    link: https://codepen.io/horizon0708/pen/dRmZbR"},{"attributes":{"name":"tictactoe","title":"Tic-Tac-Toe Game","tags":["FrontEnd","React","Redux"],"links":{"github":"https://github.com/horizon0708/react-fcc-projects"}},"body":"## About\nTic-tac-toe game originally made with Jquery ported to Redux.\n","frontmatter":"name: tictactoe\ntitle: Tic-Tac-Toe Game\ntags: \n- FrontEnd\n- React\n- Redux\nlinks:\n    github: https://github.com/horizon0708/react-fcc-projects"},{"attributes":{"name":"jackie-d","title":"Jackie D","tags":["C#","Unity"],"links":{"link":"https://play.google.com/store/apps/details?id=com.SurprisedCat.JackieD&hl=en"}},"body":"## About\nThis is a simple game written in an evening using C# and Unity. \nThe aim of the game is simple: get the Jack of Diamonds from a shuffled deck of cards as in the least amount of draws as possible.\nThat's it. \n\n## Post Mortem\n- It was amazing to see my app on my phone, working well. I would like to simplify some of my unpublished games into something simple and finished so that it can be published to the play store.\n\n","frontmatter":"name: jackie-d\ntitle: Jackie D\ntags: \n- C#\n- Unity\nlinks:\n    link: https://play.google.com/store/apps/details?id=com.SurprisedCat.JackieD&hl=en"},{"attributes":{"name":"express-auth-api","title":"Express Api Auth","tags":["Fullstack","FrontEnd","BackEnd","Express","Passport.js","Node.js","MongoDB","Bootstrap"],"links":{"github":"https://github.com/horizon0708/react-login","link":"https://quiet-sea-50693.herokuapp.com/"}},"body":"## About\nThis is a back-end RESTful API application (with a minimal front-end) that lets users sign-up and log-in for an API-key. \n\n## Goals\n- Learn about routes and local user authentication.\n- Learn about securing a simple RESTful API server with authentication. \n\n## Post Mortem\n- There are A LOT of things to consider for making an API secure, like rate-limiting, HTTPS, XORS.  \n\n## Future goals\n- Learn about more about web security, particularly HTTPS.\n- Learn how to create a front-end react app with authentication. ","frontmatter":"name: express-auth-api\ntitle: Express Api Auth\ntags: \n- Fullstack\n- FrontEnd\n- BackEnd\n- Express\n- Passport.js\n- Node.js\n- MongoDB\n- Bootstrap\nlinks:\n    github: https://github.com/horizon0708/react-login\n    link: https://quiet-sea-50693.herokuapp.com/"},{"attributes":{"name":"pomodoro","title":"Pomodoro Timer","tags":["FrontEnd","Jquery","Bootstrap"],"links":{"link":"https://codepen.io/horizon0708/pen/mwXMxP"}},"body":"## About\nThis is a front-end code-pen application that is a pomodoro timer written with Jquery.\n","frontmatter":"name: pomodoro\ntitle: Pomodoro Timer\ntags: \n- FrontEnd\n- Jquery\n- Bootstrap\nlinks:\n    link: https://codepen.io/horizon0708/pen/mwXMxP"},{"attributes":{"name":"portfolio","title":"This Portfolio","tags":["FrontEnd","React","Material UI"]},"body":"## About\nThe website you are looking at right now. The work pages are dynamically generated from markdown files.\n\n## Goals\n- Create a good-lookingg and dynamically generated portfolio.\n\n","frontmatter":"name: portfolio\ntitle: This Portfolio\ntags: \n- FrontEnd\n- React\n- Material UI"},{"attributes":{"name":"redux-bookstore","title":"Redux Bookstore","tags":["Fullstack","FrontEnd","BackEnd","React","Redux","Express","MongoDB","Nodejs"],"links":{"link":"https://vast-tundra-70796.herokuapp.com/"}},"body":"## About\nA React-Redux bookstore I made as my first Redux/Fullstack project. Users can add books to the cart.\nUsers can also perform basic CRUD operations like adding, editing and deleting books too.\n\n## Goals\n- Learn how Redux works with back-end.","frontmatter":"name: redux-bookstore\ntitle: Redux Bookstore\ntags:\n- Fullstack\n- FrontEnd \n- BackEnd\n- React\n- Redux\n- Express\n- MongoDB\n- Nodejs\nlinks:\n    link: https://vast-tundra-70796.herokuapp.com/"},{"attributes":{"name":"flux-todolist","title":"Flux ToDoList","tags":["FrontEnd","React","Flux"],"links":{"github":"https://github.com/horizon0708/TodoReactFlux","link":"https://horizon0708.github.io/TodoReactFlux/"}},"body":"\r\n## About\r\nA minimal todolist made with React and Flux.","frontmatter":"name: flux-todolist\r\ntitle: Flux ToDoList\r\ntags: \r\n- FrontEnd\r\n- React\r\n- Flux\r\n\r\nlinks:\r\n    github: https://github.com/horizon0708/TodoReactFlux\r\n    link: https://horizon0708.github.io/TodoReactFlux/"},{"attributes":{"name":"react-router-auth","title":"React Router Auth","tags":["Fullstack","FrontEnd","BackEnd","Express","Passport.js","Node.js","MongoDB","Bootstrap"],"links":{"link":"https://react-router-auth.herokuapp.com/"}},"body":"## About\nThis is a front-end application (with minimal back-end) that lets users sign-up and log-in.  Only logged in users can see super-secret-profile page. Pages are secured by using react-router and Passport.js to check for authentication before routing them to secure react components.\n\n## Goals\n- Learn how to create a react app with authentication. \n\n## Post Mortem\n- I had ran into some problems because react-router had updated to v4.\n\n## Future goals\n- Learn about OAuth.\n- Learn about more about web security.","frontmatter":"name: react-router-auth\ntitle: React Router Auth\ntags:\n- Fullstack\n- FrontEnd\n- BackEnd\n- Express\n- Passport.js\n- Node.js\n- MongoDB\n- Bootstrap\nlinks:\n    link: https://react-router-auth.herokuapp.com/"},{"attributes":{"name":"simon-game","title":"Simon Game","tags":["FrontEnd","Jquery","React","Redux"],"links":{"github":"https://github.com/horizon0708/react-fcc-projects"}},"body":"## About\nSimon games ported to React-Redux from Jquery.\n","frontmatter":"name: simon-game\ntitle: Simon Game\ntags: \n- FrontEnd\n- Jquery\n- React\n- Redux\nlinks:\n    github: https://github.com/horizon0708/react-fcc-projects"},{"attributes":{"name":"vscode-snippet-helper","title":"VSCode Snippet Helper","tags":["FrontEnd","React"],"links":{"link":"https://horizon0708.github.io/vscode-snippet-helper/","github":"https://github.com/horizon0708/vscode-snippet-helper"}},"body":"## About\nAn app to help create code snippets for Visual Studio Code.\n\n## Post Mortem\n- I made this app for myself because I make a lot of code snippets as I learn a new framework. However, as soon as I finished this project, I switched to WebStorm :(","frontmatter":"name: vscode-snippet-helper\ntitle: VSCode Snippet Helper\ntags: \n- FrontEnd\n- React\nlinks:\n    link: https://horizon0708.github.io/vscode-snippet-helper/\n    github: https://github.com/horizon0708/vscode-snippet-helper"},{"attributes":{"name":"twitter-quote-gen","title":"Twitter Quote Generator","tags":["FrontEnd","JQuery","API","Codepen"],"links":{"link":"https://codepen.io/horizon0708/pen/VbWeMj"}},"body":"## About\nA page that generates a quote and uses twitter api to share the quote to twitter.\n","frontmatter":"name: twitter-quote-gen\ntitle: Twitter Quote Generator\ntags: \n- FrontEnd\n- JQuery\n- API\n- Codepen\nlinks:\n    link: https://codepen.io/horizon0708/pen/VbWeMj"},{"attributes":{"name":"wikipedia-viewer","title":"Wikipedia Viewer","tags":["FrontEnd","React","API","Codepen"],"links":{"link":"https://codepen.io/horizon0708/pen/VWgmJQ"}},"body":"## About\nA Wikipedia viewer that uses Wikipedia API to search and link to articles.","frontmatter":"name: wikipedia-viewer\ntitle: Wikipedia Viewer\ntags: \n- FrontEnd\n- React\n- API\n- Codepen\nlinks:\n    link: https://codepen.io/horizon0708/pen/VWgmJQ"}]

